import React, { useState } from "react";
import "./Browse.css";

// Components
import Navbar from "../../components/Navbar/Navbar";
import Banner from "../../components/Banner/Banner";
import MoviesList from "../../components/MoviesList/MoviesList";

// api
import requests from "../../utils/requests";

// init open movie detail
const initOpenStatus = {
  original: false,
  trending: false,
  topRated: false,
  action: false,
  comedy: false,
  horror: false,
  romance: false,
  documentaries: false,
};

const movies = [
  {
    type: "original",
    title: "Original",
    fetchUrl: requests.fetchNetflixOriginals,
  },
  {
    type: "trending",
    title: "Xu hng",
    fetchUrl: requests.fetchTrending,
  },
  {
    type: "topRated",
    title: "Xp hng cao",
    fetchUrl: requests.fetchTrending,
  },
  {
    type: "action",
    title: "Hnh ng",
    fetchUrl: requests.fetchActionMovies,
  },
  {
    type: "comedy",
    title: "Hi",
    fetchUrl: requests.fetchComedyMovies,
  },
  {
    type: "horror",
    title: "Kinh d",
    fetchUrl: requests.fetchHorrorMovies,
  },
  {
    type: "romance",
    title: "Lng mn",
    fetchUrl: requests.fetchRomanceMovies,
  },
  {
    type: "documentaries",
    title: "Ti liu",
    fetchUrl: requests.fetchDocumentaries,
  },
];

function Browse() {
  const [openStatus, setOpenStatus] = useState(initOpenStatus);

  // set status category is open
  const onOpenDetail = (type) => {
    const changeStatus = { ...initOpenStatus };
    changeStatus[type] = true;
    setOpenStatus(changeStatus);
  };

  return (
    <div className="movies-page">
      <Navbar />
      <Banner />
      <div className="movies">
        {/* render list */}
        {movies.map((item, index) => {
          return (
            <MoviesList
              type={item.type}
              title={item.title}
              fetchUrl={item.fetchUrl}
              openStatus={openStatus}
              setOpenStatus={(e) => onOpenDetail(e)}
              key={index}
            />
          );
        })}
      </div>
    </div>
  );
}

export default Browse;
